<template>
  <div class="hello">
    <h1>{{ msg }}</h1>

    <el-row>
      <el-col :span="7">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableData">
              <el-table-column prop="foodsName" label="商品"></el-table-column>
              <el-table-column prop="count" label="数量"></el-table-column>
              <el-table-column prop="price" label="金额"></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button @click="delGoods(scope.row)" type="text" size="small">移除</el-button>
                  <el-button @click="addOrderList(scope.row)" type="text" size="small">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="外卖">外卖</el-tab-pane>
          <el-tab-pane label="自取">自取</el-tab-pane>
        </el-tabs>

        <div class="calculation">
          <label>数量：{{totalCount}}</label>
          <span>金额：{{totalMoney}}</span>
        </div>

        <div style="margin-top:20px;">
          <el-button @click="emptyShopCar" type="primary">清空</el-button>
          <el-button @click="payMoney" type="success">结算</el-button>
        </div>
      </el-col>

      <el-col :span="17">
        <div class="daily-goods">
          <div class="title">日常商品</div>
        </div>

        <div class="daily-goods-list">
          <ul>
            <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
              <span>{{ goods.foodsName }}</span>
              <span>{{ goods.price }}元</span>
            </li>
          </ul>
        </div>

        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <ul class="cookList">
                <li v-for="goods in foods_type" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img v-bind:src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.foodsName}}</span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <ul class="cookList">
                <li v-for="goods in foods_type1" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img v-bind:src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.foodsName}}</span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="薯条">
              <ul class="cookList">
                <li v-for="goods in foods_type2" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img v-bind:src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.foodsName}}</span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="奶茶">
              <ul class="cookList">
                <li v-for="goods in foods_type3" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img v-bind:src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.foodsName}}</span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Slider",
  data() {
    return {
      msg: "Vue+ElementUI 点菜应用",
      tableData: [],
      totalCount: 0,
      totalMoney: 0,
      oftenGoods: [
        {
          goodsId: 2,
          foodsName: "香辣鸡腿堡",
          price: 15
        },
        {
          goodsId: 3,
          foodsName: "原味鸡腿堡",
          price: 15
        },
        {
          goodsId: 4,
          foodsName: "奥尔良鸡腿堡",
          price: 18
        },
        {
          goodsId: 5,
          foodsName: "可口可乐",
          price: 10
        },
        {
          goodsId: 6,
          foodsName: "雪碧柠檬味",
          price: 20
        },
        {
          goodsId: 7,
          foodsName: "芬达橙味汽水",
          price: 10
        },
        {
          goodsId: 8,
          foodsName: "经典炸薯条",
          price: 18
        },
        {
          goodsId: 9,
          foodsName: "比利时炸薯条",
          price: 15
        },
        {
          goodsId: 10,
          foodsName: "番茄奶酪薯条",
          price: 17
        }
      ],
      foods_type: [
        {
          goodsId: 11,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t1/101776/21/16819/138244/5e8003bcEf2be4def/423b124c0e641e88.jpg",
          foodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 12,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t1/87292/16/4555/355931/5de759a7Ecba923b9/844542c22995a105.jpg",
          foodsName: "原味鸡腿堡",
          price: 15
        },
        {
          goodsId: 13,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t18643/146/2407861968/202851/2bf4579b/5af53b1eNf714372c.jpg",
          foodsName: "奥尔良鸡腿堡",
          price: 15
        }
      ],
      foods_type1: [
        {
          goodsId: 16,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t8302/147/1031208510/386450/47ce1a27/59b2c42fNed4d2ece.jpg",
          foodsName: "可口可乐",
          price: 20
        },
        {
          goodsId: 13,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t7777/262/2671675393/386949/80125863/59b2c3fdNd2f9abf9.jpg",
          foodsName: "雪碧柠檬味",
          price: 15
        },
        {
          goodsId: 13,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t7864/216/2712097427/400568/db980658/59b2c40bN7e761cd8.jpg",
          foodsName: "芬达橙味汽水",
          price: 15
        }
      ],
      foods_type2: [
        {
          goodsId: 16,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t1/58149/20/13822/456340/5da919c4Eb954fd95/c53a0a1e7d93e71f.jpg",
          foodsName: "经典炸薯条",
          price: 20
        },
        {
          goodsId: 13,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t1/92203/22/15154/383100/5e707d4aEfa8137ea/01d8232edc1488da.jpg",
          foodsName: "比利时炸薯条",
          price: 15
        },
        {
          goodsId: 13,
          goodsImg:
            "https://img14.360buyimg.com/n0/jfs/t1/61396/11/13178/461164/5da91f14E7d2f6e16/446672fded45a1a9.jpg",
          foodsName: "番茄奶酪薯条",
          price: 15
        }
      ],
      foods_type3: [
        {
          goodsId: 17,
          goodsImg:
            "http://www.laio8.com/static/index/uploads/img/20180509/a4f76f8cf7783f8c9415d8f1eff8b671.png",
          foodsName: "珍珠奶茶",
          price: 10
        },
        {
          goodsId: 18,
          goodsImg:
            "http://www.laio8.com/static/index/uploads/img/20180509/35375ceb4dd0cf413a91afe0b9784165.png",
          foodsName: "金桔柠檬汁",
          price: 18
        },
        {
          goodsId: 19,
          goodsImg:
            "http://www.laio8.com/static/index/uploads/img/20180509/cbeb2e5bd2fe6b06068db146149c71f9.png",
          foodsName: "红豆巧克力",
          price: 15
        }
      ]
    };
  },
  methods: {
    deleteRow(n) {
      console.log(n.goods);
    },
    addRow(n) {
      console.log(n.goods);
    },
    // 增加商品
    addOrderList(_goodsObj) {
      // console.log(n)
      // 某商品是否已经存在的标识
      let _is = false;

      //判断整个订单列表当中，是否已经存在某个商品
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId === _goodsObj.goodsId) {
          // 订单中已经有相同的商品了，不再循环
          _is = true;
          break;
        }
      }

      // 如果存在，_is为true，过滤出已存在的商品，比较id号
      if (_is) {
        let _temp = this.tableData.filter(
          _g => _g.goodsId == _goodsObj.goodsId
        );
        // console.log( _temp );
        _temp[0].count++;
      } else {
        // 如果商品不存在
        let newGoods = {
          goodsId: _goodsObj.goodsId,
          foodsName: _goodsObj.foodsName,
          price: _goodsObj.price,
          count: 1
        };

        this.tableData.push(newGoods);
      }
      // 总金额计算
      this.getAllMoney();
    },
    //移除商品
    delGoods(_goodsObj) {
      this.tableData = this.tableData.filter(
        _g => _g.goodsId != _goodsObj.goodsId
      );
    },
    // 删除所有商品
    emptyShopCar() {
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    getAllMoney() {
      // 每次计算所有商品，归0
      this.totalCount = 0;
      this.totalMoney = 0;

      if (this.tableData) {
        this.tableData.forEach(_n => {
          // console.log( _n )
          this.totalCount += _n.count;
          this.totalMoney = this.totalMoney + _n.price * _n.count;
        });
      }
    },
    // 结算
    payMoney() {
      this.$confirm(
        `商品数量：${this.totalCount}，
         总价：${this.totalMoney}， 是否支付？`,
        "提交订单",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }
      )
        .then(() => {
          this.$message({
            type: "success",
            message: "支付成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消支付"
          });
        });
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
  height: 18px;
  border-bottom: 2px solid #e4e7ed;
  background-color: #f9fafc;
  text-align: center;
  padding: 10px;
}
.daily-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #58b7ff;
}
.goods-type {
  clear: both;
  cursor: pointer;
}
.calculation {
  margin-top: 20px;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
}
.cookList li span {
  display: block;
  float: left;
}
.cookList li span.foodImg {
  width: 100px;
  height: 100px;
  overflow: hidden;
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
</style>



